@import "../../styles/styles";

.banner-split {
  display: flex;
  width: 100%;
  height: 415px;
  margin-bottom: 100px;

  .banner-split-left {
    width: 50%;
    margin-right: 1rem;

    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
    }
  }

  .banner-split-right {
    width: 50%;
    background-color: $primary-grey;
    border-radius: 10px;
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 8em 4em;

    h2 {
      @extend %sub-heading;
      margin-bottom: 40px;
    }

    .banner-split-nav {
      button {
        @include button-one;
        background-color: transparent;
        border: 2px solid black;
        color: black;
        font-weight: $font-bold;

        &:hover {
          background-color: black;
          color: white;
        }
      }
    }
  }
}
