@import "../../styles/styles";

.stepper {
    display: flex;
    margin: 0;
    padding: 0;
    width: 120px;
    height: 48px;
    justify-content: center;
    align-items: center;
    background-color: $primary-grey;

    p {
        flex: 1;
        text-align: center;
    }
    button {
        width: 30px;
        flex: 1;
        height: 100%;
        border: none;
        cursor: pointer;
        opacity: 50%;

        &:hover {
            color: $primary-brownish-orange;
            font-weight: $font-extra-bold;
        }
    }
}