@import "../../../styles/styles";

.simple-input-container {

    .simple-input-header {
        display: flex;
        justify-content: space-between;
        padding-right: 20px;
        align-items: center;
        p {
            font-weight: $font-bold;
            margin-bottom: 10px;
        }
    }
        
    input {
        display: block;

        height: 50px;
        padding: 1em 1em;
        border-radius: 10px;
        border: 2px solid rgb(215, 214, 214);
        font-size: 16px;
        opacity: 50%;
        font-weight: $font-normal;
        min-width: 450px;
        margin-right: 20px;

        &:focus {
            border: 2px solid $primary-brownish-orange;
            outline: none;
        }
    }
    margin-bottom: 20px;
}